.header{
    display: flex;
    align-items: center;
    color: white;
    background-color: black;
    position: sticky;
    top: 0;
    z-index: 2000;
}
.header-left{
    flex: 0.4;
    
}

.header-left >h1
{
    text-align: center;
    letter-spacing: 1px;
    padding: 20px 0;
}
.header-left > h1 >span{
    color: red;
}
.header-right{
    display: flex;
    flex: 0.6;
    justify-content: space-evenly;
    align-items: center;
    
}
.header-right > a > h4{
    font-size: 18px;
    cursor: pointer;
    letter-spacing: 1px;
    transition:  0.3s ease-in-out;
    position: relative;
}

/* underline */
.header-right > a > h4::before{
    height: 3px;
    width: 100%;
    background-color: white;
    bottom: -10px;
    position: absolute;

}

.header-right > a > h4:hover{
    transform: scale(1.07);
    color: white;
    background: orangered;
    border-radius: 2px;
    padding: 5px 5px;
    letter-spacing: 2px;
}
.header-right-button{
    background-color: orangered;
    color: white;
    border-radius: 3px;
    padding: 3px 5px;
    cursor: pointer;
}
/* menu */
.nav-icon{
    cursor: pointer;
    display: none;
    font-size: 1.5rem;
}

@media screen  and (max-width:925px){
    .nav-icon{
        display: flex;
        position: absolute;
        right: 20px;
    }
    .header-right{
        display: flex;
        flex-direction: column;
        position: absolute;
        text-align: center;
        width: 100%;
        top: 70px;
        left: 100%;
        background-color: black;
        padding: 10px 0;
    }
    .nav-item h4{
        border-bottom: 1px solid white;
        padding: 10px;
    }
    .nav-item:nth-child(1){
        border-top: 1px solid white;
    }
    .header-right.active{
        left: 0%;
    }
    
}