.contact-container{
    color: white;
    margin-top: 200px;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.contact-heading{
    display: flex;
    justify-content: center;
}
.contact-details{
    margin: 30px 450px;
    border: dotted thistle;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 50px;
    padding: 20px;
}
.contact-details>h2{
    font-size: 20px;
    text-align: left;

    display: flex;
    justify-content: center;
    margin-bottom: 30px;
}
.contact-details>h2>span{
    margin-left: 10px;
}

.contact-icons{
    padding: 20px 0;
    color: red;
    margin: 30px;
}